import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			config: {
				type: 1, //0无title，1有title
				title: "搜索",
				back: true,
			},
			historys: [],
			key: '',
			type:1,
			listArr: ['出租', '出售', '求租', '求售', '找室友'],
			listActive: 0, // 当前选中项
			tabsScrollLeft: 0, // tabs当前偏移量
			scrollLeft: 0,
			lineStyle: {}, // 下划线位置--动态甲酸
			duration: 0.2, // 下划线动画时长
		}
	},
	components: {
		navigation
	},
	onShow() {
		this.key = '';
		this.gethistory();
	},
	watch: {
	  	listActive(newVal) {     // 监听当前选中项
	  		this.setTabList()
	  	}
	},
	mounted () {
	      this.setTabList()
	},
	methods: {
		customConduct() {
			uni.navigateBack({});
		},
		handlesearch() {
			if (this.key != '') {
				this.historys.push(this.key)
				uni.setStorageSync('historyword', JSON.stringify(this.historys));
				uni.navigateTo({
					url:'../order/orderlist?key='+this.key+'&type='+this.type
				})
			}
		},
		tapsearch(key) {
			this.historys.push(key)
			uni.setStorageSync('historyword', JSON.stringify(this.historys));
			uni.navigateTo({
				url:'../order/orderlist?key='+key+'&type='+this.type
			})
		},
		gethistory() {
			if (uni.getStorageSync('historyword')) {
				this.historys = JSON.parse(uni.getStorageSync('historyword'));
			}
		},
		deletehistory(e) {
			this.historys.splice(e, 1);
			uni.setStorageSync('historyword', JSON.stringify(this.historys));
		},
		clickSort(index) {
			this.listActive = index
			this.type = index + 1
		},
		// scroll-view滑动事件
		scroll(e) {
			this.scrollLeft = e.detail.scrollLeft;
		},
		setTabList() {
			this.$nextTick(() => {
				this.setLine()
				this.scrollIntoView()
			})
		},
		// 计算tabs位置
		scrollIntoView() { // item滚动
			let lineLeft = 0;
			this.getElementData('#tab_list', (data) => {
				let list = data[0]
				this.getElementData(`#tab_item`, (data) => {
					let el = data[this.listActive]
					lineLeft = el.width / 2 + (-list.left) + el.left - list.width / 2 - this.scrollLeft
					this.tabsScrollLeft = this.scrollLeft + lineLeft
				})
			})
		},
		//  计算下划线位置
		setLine() {
			let lineWidth = 0,
				lineLeft = 0
			this.getElementData(`#tab_item`, (data) => {
				let el = data[this.listActive]
				lineWidth = el.width / 2
				lineLeft = el.width / 2 + (-data[0].left) + el.left
				this.lineStyle = {
					width: `${lineWidth}px`,
					transform: `translateX(${lineLeft}px) translateX(-50%)`,
					transitionDuration: `${this.duration}s`
				};
			})
		},
		getElementData(el, callback) {
			uni.createSelectorQuery().in(this).selectAll(el).boundingClientRect().exec((data) => {
				callback(data[0]);
			});
		},
	}
}
